import { useContext } from "react";
import { Button } from "@mui/material";
import { useQuery } from "react-query";
import { AxiosInstance } from "axios";
import { AuthContext, User } from "../../components/AuthContext";

async function fetchUser(myAxios: AxiosInstance, user: User | null) {
  return await myAxios.get("/user", {
    headers: { Authorization: `Bearer ${user?.accessToken}` },
  });
}
export default function Dashboard() {
  const { user, myAxios } = useContext(AuthContext);
  const { data, refetch } = useQuery(
    "users",
    async () => await fetchUser(myAxios, user),
    {
      enabled: false,
    }
  );

  function handleClick() {
    refetch();
  }
  console.log(data)
  return (
    <div>
      <h1 style={{ color: "black" }}>Test</h1>
      <p style={{ color: "black" }}>qweqweqwe</p>
      <Button onClick={handleClick}>Click</Button>
    </div>
  );
}
